import {
	createRouter,
	createWebHistory
} from 'vue-router'


export const router = createRouter({
	history: createWebHistory(),
	routes: [{
			name: 'home',
			path: '/home',
			component: import('./views/home')
		},
		{
			name: 'tianqi',
			path: '/tianqi',
			component: import('./views/tianqi')
		},
		{
			name: 'phone',
			path: '/phone',
			component: import('./views/phone')
		},
		{
			name: 'other',
			path: '/other',
			component: import('./views/other')
		},
		{
			name: 'compute',
			path: '/compute',
			component: import('./views/compute')
		},
	]
})
